import { useSelector } from "react-redux";
import IMAGES from "../../../assets/images";

function PrescriptionPDF(props) {
  const { prescriptionData } = props;

  const { getUserData } = useSelector((state) => state.userRole);
  return (
    <>
      <div className="d-flex justify-content-center mb-4">
        <img
          src={IMAGES.LOGO}
          alt="FamilyDoc 24/7 Logo"
          style={{ width: "22%" }}
        />
      </div>
      <div className="text-center">
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>77 Camden Street Lower, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : familydoc.gp@healthmail.ie</h6>
      </div>
      <div className="pdf-table">
        {prescriptionData?.medicineList?.length > 0 ? (
          <>
            <div className="my-5">
              <table>
                <thead>
                  <tr>
                    <th>Medicine</th>
                    <th>Formulation</th>
                    <th>Dose</th>
                    <th>Frequency</th>
                    <th>Duration</th>
                  </tr>
                </thead>
                <tbody>
                  {prescriptionData?.medicineList?.map((item, index) => (
                    <tr key={index}>
                      <td>{item.medicineName}</td>
                      <td>{item.formulation}</td>
                      <td>{item.dose}</td>
                      <td>{item.doseFrequency}</td>
                      <td>{item.quantity}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className="mt-4 mb-5">
                <h5>Notes:</h5>
                <ul>
                  {prescriptionData?.medicineList.map((item, index) => (
                    <li key={index}>{item.note ? item.note : "N/A"}</li>
                  ))}
                </ul>
              </div>
            </div>
          </>
        ) : (
          <p className="record-message">No Records to Display</p>
        )}
      </div>
      <div className="d-flex">
        <div className="me-5">
          <p>
            MCRN{" "}
            {prescriptionData?.mcrn
              ? prescriptionData?.mcrn
              : getUserData?.mcrn
              ? getUserData?.mcrn
              : "N/A"}
          </p>

          <p className="mb-0">
            Dr{" "}
            {prescriptionData?.doctorName
              ? prescriptionData?.doctorName
              : getUserData?.name
              ? getUserData?.name
              : "N/A"}
          </p>
        </div>
        {getUserData?.doctorSignature || prescriptionData?.doctorSignature ? (
          <span
            style={{
              display: "inline-block",
              width: "100px",
              height: "50px",
            }}
          >
            <img
              src={
                getUserData?.doctorSignature ||
                prescriptionData?.doctorSignature
              }
              alt="signature"
              width="140px"
              height="100px"
            />
          </span>
        ) : (
          <p className="text-decoration-underline">No Signature Found</p>
        )}
      </div>
    </>
  );
}

export default PrescriptionPDF;
