import moment from "moment";
import IMAGES from "../../../assets/images";

export default function InvoicePDF(props) {
  const { invoiceData } = props;

  return (
    <>
      <img
        src={IMAGES.LOGO}
        alt="FamilyDoc 24/7 Logo"
        style={{ width: "22%" }}
      />
      <div className="w-100 d-flex justify-content-between align-items-center my-3">
        <div className="d-flex align-items-center">
          <div className="px-3">
            <div style={{ color: "#999999" }}>
              Invoice: {invoiceData?.invoiceNumber}
            </div>
            <div style={{ color: "#999999" }}>
              Created Date:{" "}
              {moment(invoiceData?.createdDate).format("DD MMM YYYY")}
            </div>
          </div>
        </div>
        <h6>{invoiceData?.status}</h6>
      </div>
      <hr style={{ color: "#c8cfd5" }} />
      <div className="mt-3 d-flex justify-content-between">
        <div>
          <label style={{ color: "#B3B3B3" }}>From:</label>
          <h5 className="fs-6 fw-bold">{invoiceData?.patientName}</h5>
          <p className="Address-FontSize fw-bold mb-2">
            {invoiceData?.location}
          </p>
          <small>{invoiceData?.email}</small>
          <p className="Address-FontSize mt-2">{invoiceData?.phoneNumber}</p>
        </div>

        <div>
          <label style={{ color: "#B3B3B3" }}>Bill to:</label>
          <h5 className="fs-6 fw-bold">FamilyDoc Medical Services Limited</h5>
          <p className="Address-FontSize fw-bold mb-2">
            Address: 77 Camden street Lower, Dublin 2
          </p>
          <small>contact@familydoc247.ie</small>
          <p className="Address-FontSize mt-2">+353 19069327</p>
        </div>
      </div>
      <hr style={{ color: "#c8cfd5" }} />
      <div>
        <h5 className="fs-6 fw-bold mt-4">Description</h5>
        <div className="d-flex justify-content-between">
          <h6 className="mb-2" style={{ color: "#B3B3B3" }}>
            Item
          </h6>
          <h6 className="pe-5 mb-2" style={{ color: "#B3B3B3" }}>
            Fee
          </h6>
        </div>
      </div>
      <div className="fs-6 fw-bold d-flex justify-content-between">
        <h6>Medical Services Receipt</h6>
        <h6 className="me-5">
          {invoiceData?.amount ? `â‚¬${invoiceData?.amount}` : "N/A"}
        </h6>
      </div>
    </>
  );
}
