import React from "react";
import { Modal, Form, Button } from "react-bootstrap";
import logo from "../../../assets/images/logo.png";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { PiDownloadSimpleBold } from "react-icons/pi";

function MedicationModel(props) {
  const { show, handleClose, viewPresctionData } = props;
  // const printButtonRef = useRef(null);

  const handlePrintClick = () => {
    props.onDownload(viewPresctionData);
  };

  const columns1 = [
    {
      dataField: "medicineId",
      text: "Sr",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "medicineName",
      text: "Medicines",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "formulation",
      text: "Formulation",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "dose",
      text: "Dose",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "doseFrequency",
      text: "Frequency",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "quantity",
      text: "Quantity",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "route",
      text: "Route",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
  ];
  const customTotal1 = (from, to, size) =>
    viewPresctionData?.medicineList?.length > 0 && (
      <span className="react-bootstrap-table-pagination-total">
        <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
      </span>
    );

  const paginateOptions1 = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal1,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "6",
        value: 4,
      },
    ],
  };
  return (
    <div>
      <Modal
        show={show}
        onHide={handleClose}
        centered
        size="xl"
        className="Modal-MainClass"
      >
        <Modal.Header className="p-4">
          <span>
            <img src={logo} alt="family doc" />
            <p className="mt-2 mb-0">contact@familydoc247.ie | www.familydoc247.ie</p>
          </span>
          <span className="text-end color-99">
            <h2 className="font-weight-600 mb-0" style={{ color: "#1A1A1A" }}>
              {viewPresctionData?.doctorName}
            </h2>
            <h4 className="mb-0">{viewPresctionData?.doctorId}</h4>
          </span>
        </Modal.Header>
        <Modal.Body className="px-4">
          <div className="w-100 d-flex justify-content-between align-items-center">
            <p
              style={{
                color: "#999999",
                fontSize: "17px",
                letterSpacing: "0.5",
              }}
            >
              Prescription ID:
              <span
                style={{ color: "#1A1A1A", fontSize: "18px" }}
                className="fw-bold ms-2"
              >
                {viewPresctionData?.prescriptionId}
              </span>
            </p>
            <p
              style={{ color: "#999999" }}
              className="Prescription_SpecificResponsive"
            >
              Name:
              <span
                style={{ color: "#1A1A1A", fontSize: "18px" }}
                className="fw-bold ms-2"
              >
                {viewPresctionData?.patientName} /{" "}
                {viewPresctionData?.patientGender} /
                {viewPresctionData?.prescribedDate.split("T")[0]}
              </span>
            </p>
          </div>
          <div className="d-flex justify-content-end mb-2">
            <Button
              className="border-0 px-3 py-2 rounded-2"
              style={{
                background: "#F0F0F0",
                color: "#1A1A1A",
              }}
              onClick={handlePrintClick}
            >
              <PiDownloadSimpleBold size={20} /> Download PDF
            </Button>
          </div>
          <span className="doctor-datatable ">
            <BootstrapTable
              columns={columns1}
              data={
                viewPresctionData?.medicineList
                  ? viewPresctionData?.medicineList
                  : []
              }
              keyField="id"
              id="bar"
              pagination={paginationFactory(paginateOptions1)}
              bordered={false}
              wrapperClasses="table-responsive"
            />
          </span>
          <Form.Group
            style={{ fontSize: "18px", fontWeight: 500 }}
            controlId="exampleForm.ControlTextarea1"
          >
            <Form.Label className="mb-0">Recommended Actions</Form.Label>
            <Form.Control
              as="textarea"
              rows={2}
              placeholder="Tell about some recommended actions"
              className="pt-2 mb-2"
              readOnly={true}
              defaultValue={
                viewPresctionData?.recommendedActions
                  ? viewPresctionData?.recommendedActions
                  : "N/A"
              }
            />
          </Form.Group>
        </Modal.Body>
        {/* <Modal.Footer>
          <Button
            variant="primary"
            onClick={handleClose}
            className="px-5 pe-5"
            style={{ backgroundColor: "#000071" }}
          >
            <BsDownload className="me-2" />
            Download
          </Button>
        </Modal.Footer> */}
      </Modal>
    </div>
  );
}

export default MedicationModel;
