import React, { useEffect, useMemo, useRef, useState } from "react";
import { Button, Card, Col, Row } from "react-bootstrap";
import { AiFillEye } from "react-icons/ai";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";

import { PATH } from "../../../utils/path";
import { getPrescriptionList } from "../../../app/features/patientPrescription/patientPrescription.slice";
import MedicationModel from "../Prescription/MedicationModel";
import PrescriptionPDF from "../../../components/Theme/PrescriptionPDF";
import DataTable from "../../../components/Table/DataTable";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import SearchInput from "../../../components/SearchInput";

export default function CurrentPrescriptionDatatable() {
  const [show, setShow] = useState(false);
  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = useMemo(() => 3, []);
  const [viewPresctionData, setViewPresctionData] = useState();
  const [searchValue, handleSearch] = useDebouncedSearch(500);

  const printButtonRef = useRef(null);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { allPrescription, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.patientPrescription
  );
  const { user } = useSelector((state) => state.auth);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const printTimeOut = (data) => {
    setShow(false);
    setTimeout(function () {
      printButtonRef?.current?.click();
    }, 1000);
  };

  const viewPrescription = (data) => {
    handleShow();
    setViewPresctionData(data);
  };

  useEffect(() => {
    const finalData = {
      pageNo: pageNumber,
      size: pageSize,
      isPagination: true,
      patientId: user?.userId,
      search: searchValue,
    };
    dispatch(getPrescriptionList({ finalData }));
  }, [dispatch, searchValue, user?.userId, pageNumber]);

  const columns = [
    {
      dataField: "prescriptionId",
      text: "ID",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "doctorName",
      text: "Doctor Name",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "medicine",
      text: "Medicine",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "prescribedDate",
      text: "Prescribed Date",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, col) => {
        return (
          <p className="m-0">
            {moment(col?.prescribedDate).format("DD/MM/YYYY")}
          </p>
        );
      },
    },
    {
      dataField: "action",
      text: "Actions",
      sort: false,
      formatter: (row, data) => (
        <Link className="mx-2">
          <AiFillEye
            className="me-2"
            style={{ color: "#3F8BFC" }}
            size={18}
            onClick={() => viewPrescription(data)}
          />
          {/* <RiDeleteBin5Fill
            className="ms-2"
            style={{ color: "#C9273A" }}
            size={18}
            onClick={() => {
              setDeleteId(cell?.prescriptionId);
              setShowDeleteModal(true);
            }}
          /> */}
        </Link>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };
  return (
    <>
      <Card
        className="pt-3 mb-0 payment-dashboard-table"
        style={{ height: "390px" }}
      >
        <Card.Body className="p-0">
          <div className="mx-3 d-flex flex-wrap justify-content-between align-items-center">
            <Card.Title>Current Prescription</Card.Title>
            <div className="d-flex flex-wrap justify-content-end selects-section">
            <SearchInput onChange={handleSearch} className="mb-3" />
              <Button
                variant="outline-secondary"
                className="custom-outline-btn mb-3"
                onClick={() => navigate(PATH.PATIENT_PRESCRIPTION)}
              >
                View All
              </Button>
            </div>
          </div>
          {isLoading ? (
            <FullScreenLoader fullHeight={true} />
          ) : isSuccess ? (
            <span className="patient-datatable">
              <DataTable
                tableColumns={columns}
                tableData={allPrescription?.data ? allPrescription?.data : []}
                keyField="prescriptionId"
                pageNumber={pageNumber}
                totalRecords={allPrescription?.totalCounts}
                dataPerPage={pageSize}
                handlePageChange={handlePageChange}
              />
            </span>
          ) : isError ? (
            <p className="text-center text-danger fst-italic my-5">
              Network Error
            </p>
          ) : null}
        </Card.Body>
      </Card>
      <MedicationModel
        viewPresctionData={viewPresctionData}
        handleClose={handleClose}
        onDownload={printTimeOut}
        show={show}
      />
      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="border-0 px-3 py-2 rounded-2 d-none"
          >
            <PiDownloadSimpleBold size={20} /> Download PDF
          </button>
        }
      >
        <PrescriptionPDF prescriptionData={viewPresctionData} />
      </PrintComponents>
    </>
  );
}
