import React, { useEffect } from "react";
import { Card, Row, Col } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import CurrentPrescriptionDatatable from "./CurrentPrescriptionDataTable";
import Calendar from "./Calendar";
import PaymentInfoDatatable from "./PaymentInfoDataTable";
import { getTotalAppointments } from "../../../app/features/patientDashboard/patientDashboard.slice";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../utils/path";

export default function Dashboard() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { totalAppointments, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.patientDashboard
  );

  useEffect(() => {
    dispatch(getTotalAppointments());
  }, [dispatch]);

  return (
    <>
      <Row className="main-row patient-dashbaord">
        <Col xl={4} md={6} xs={12} className="mb-3">
          <Card
            className="h-100 patient__nav-card"
            onClick={() => navigate(PATH.DOCTOR_DETAILS)}
          >
            <Card.Body className="p-4">
              <Card.Title className="m-0">Book Appointment</Card.Title>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={4} md={6} xs={12} className="mb-3">
          <Card
            className="h-100 patient__nav-card"
            onClick={() => navigate(PATH.PATIENT_PRESCRIPTION_FORMS)}
          >
            <Card.Body className="p-4">
              <Card.Title className="m-0">Request Prescription</Card.Title>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={4} xs={12} className="mb-3">
          <Card
            className="h-100 patient__nav-card"
            onClick={() => navigate(PATH.PATIENT_PLANS)}
          >
            <Card.Body className="p-4">
              <Card.Title className="m-0">My Plans</Card.Title>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="main-row patient-dashbaord">
        <Col sm={6} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Appointments</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {totalAppointments && totalAppointments?.appointmentCount}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.APPOINTMENT_ICON}
                alt="appointment icon"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={6} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Upcoming Appointments</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {totalAppointments &&
                      totalAppointments?.upcomingAppointmentCount}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.APPOINTMENT_ICON}
                alt="new patient icon"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="my-3">
        <Col xl={8} xs={12}>
          <Row>
            <Col xs={12} className="mb-3">
              <CurrentPrescriptionDatatable />
            </Col>
            <Col xs={12} className="mb-3">
              <PaymentInfoDatatable />
            </Col>
          </Row>
        </Col>
        <Col xl={4} xs={12}>
          <Row>
            <Col xs={12}>
              <Calendar />
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
}
