import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

import PropTypes from "prop-types";

export default function DataTable(props) {
  const {
    keyField,
    tableColumns,
    tableData,
    pageNumber,
    totalRecords,
    handlePageChange,
    dataPerPage,
  } = props;

  const emptyDataMessage = () => {
    return <p className="record-message">No Records to Display</p>;
  };

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      {tableData?.length > 0 ? `${from} to ${to} out of ${size} entries` : null}
    </span>
  );

  const paginateOptions = {
    paginationSize: 5,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: tableData?.length > 5 ? true : false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: dataPerPage,
        value: dataPerPage,
      },
    ],
  };

  return (
    <BootstrapTable
      keyField={keyField}
      headerClasses="header-class"
      data={tableData}
      columns={tableColumns}
      pagination={paginationFactory({
        ...paginateOptions,
        page: pageNumber,
        totalSize: totalRecords,
        onPageChange: handlePageChange,
      })}
      noDataIndication={emptyDataMessage}
      remote
      onTableChange={() => {}}
      bordered={false}
      wrapperClasses="table-responsive"
    />
  );
}
DataTable.propTypes = {
  tableData: PropTypes.array.isRequired,
  tableColumns: PropTypes.array.isRequired,
  keyField: PropTypes.node.isRequired,
  pagination: PropTypes.bool,
  paginateOptions: PropTypes.object,
};
DataTable.defaultProps = {
  pagination: true,
};
